import { Component, OnInit } from '@angular/core';
import { User } from 'src/app/models/User';
import { DataServiceService } from 'src/app/data-service.service';
import { ActivatedRoute, Router } from '@angular/router';
import { EditUserComponent } from './edit-user/edit-user.component';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
  users:Array<User>;
  selectedUser:User;
  newUser:User=null;
  action:string;
  loadingData=true;
  message:string='Please wait .. .';
  refreshTimer=0;
  constructor(private dataService:DataServiceService,private routes:ActivatedRoute,private router:Router  
              ) { }

  ngOnInit() {   
     this.loadUserData();
  }

  processAfterLoadingData(){
    this.routes.queryParams.subscribe(
      (params)=>{
          const id=params['id'];
          const action=params['action'];
          this.action=action;
           
          if(id){
             //---------------------------------------------------
              this.selectedUser=this.users.find(
                 (user)=>{
                    return user.id===+id;
                 }
              );
             
            //-----------------------------------------------------
          }

      }
    );
  }

  loadUserData(){
    this.dataService.getUsers().subscribe(
      (users)=>{
        this.users=users;
        this.processAfterLoadingData();
        this.loadingData=false;
      },
      (error)=>{
          this.refreshTimer++;
          if(this.refreshTimer<10){
            this.message='Sorry! Something went wrong. Connecting again ...  '+this.refreshTimer;
             this.loadUserData();
          }
          else{
            this.message='Sorry! Please contact the support team.';
          }
      }
    )
  }

  selectUser(id:number){
      this.router.navigate(['admin','users'],{queryParams:{id:id,action:'view'}});
     /*this.selectedUser=this.users.find(
       (user)=>{
          return user.id===id;
       }
     );*/
  } 

  addUser(){
    this.selectedUser=null;
    this.router.navigate(['admin','users'],{queryParams:{action:'add'}});
  }
}
