import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Room } from 'src/app/models/Room';
import { Router } from '@angular/router';
import { DataServiceService } from 'src/app/data-service.service';

@Component({
  selector: 'app-room-detail',
  templateUrl: './room-detail.component.html',
  styleUrls: ['./room-detail.component.css']
})
export class RoomDetailComponent implements OnInit {

  @Input()
  roomDetailComponent:Room;

  @Output()
  deleteEvent=new EventEmitter();
  message:string='';
  constructor(private router:Router,private dataService:DataServiceService) { }

  ngOnInit() {
  }

  editRoom(){
      this.router.navigate(['admin','rooms'],{queryParams:{action:'edit',id:this.roomDetailComponent.id}});
  }

  deleteRoom(){
     this.dataService.deleteRoom(this.roomDetailComponent.id).subscribe(
        room=>{
          this.deleteEvent.emit();
          this.router.navigate(['admin','rooms']);
           },
          error=>{this.message='Something not correct!!'}
          
       );
  
  }
}
