import { Injectable } from '@angular/core';
import { Room, LayoutCapacity, Layout } from './models/Room';
import { User } from './models/User';
import { Observable, of } from 'rxjs';
import { Booking } from './models/Booking';
import { environment } from 'src/environments/environment';
import { formatDate } from '@angular/common';
import { HttpClientModule, HttpClient } from '@angular/common/http';
import {map} from 'rxjs/operators';
import { BookingsComponent } from './bookings/bookings.component';

@Injectable({
  providedIn: 'root'
})

export class DataServiceService {
  constructor(private http:HttpClient) { 
  }

  getRooms():Observable<Room[]>{
    return this.http.get<Room[]>(environment.restUrl+"/api/rooms")
    .pipe(
      map( data =>{
          const roomArray=new Array<Room>();
             for(const r of data){
               roomArray.push(Room.fromHttp(r));
             }
          return roomArray;
      })
    );
  }

  getUsers():Observable<Array<User>>{
    return this.http.get<User[]>(environment.restUrl+"/api/users")
    .pipe(
      map( data =>{
        return User.arrayFromHttp(data);
      })
    );
  }

  getBooking(id:number):Observable<Booking>{
    return this.http.get<Booking>(environment.restUrl+"/api?id="+id)
      .pipe(
        map( data=>Booking.fromHttp(data))
      );
  }

  updateUser(user:User):Observable<User>{
    console.log(user);
    return this.http.put<User>(environment.restUrl+"/api/user",user);
  }

  addUser(newAngularUser:User,pass:string):Observable<User>{
    const newSpringUser={id:newAngularUser.id,name:newAngularUser.name,password:pass};
    return this.http.post<User>(environment.restUrl+'/api/user',newSpringUser);
  }

  updateRoom(room:Room):Observable<Room>{
    const correctRoom={id:room.id,name:room.name,location:room.location,capacities:[]};
    for(const lc of room.capacities){
       let correctLayout;
        for(let member in Layout){
          if(Layout[member]===lc.layout)
            correctLayout=member;
        }

        const correctedLayout={layout:correctLayout,capacity:lc.capacity};
        correctRoom.capacities.push(correctedLayout);
    }
    return this.http.put<Room>(environment.restUrl+"/api/room",correctRoom);
  }

  addRoom(room:Room):Observable<Room>{
    const correctedRoomToBeSend={id:room.id,name:room.name,location:room.location,capacities:[]};
    for(const lc of room.capacities){
       let correctLayout;
        for(const member in Layout){
           if(lc.layout===Layout[member])
              correctLayout=member;
        }
        const correctedLayoutCapacity={layout:correctLayout,capacity:lc.capacity};
        correctedRoomToBeSend.capacities.push(correctedLayoutCapacity);
    }
    return this.http.post<Room>(environment.restUrl+"/api/room",correctedRoomToBeSend);
  }
  
  deleteRoom(id:number):Observable<any>{
    return this.http.delete(environment.restUrl+"/api/deleteRoom/"+id);
  }

  deleteUser(id:number):Observable<any>{
    return this.http.delete(environment.restUrl+"/api/deleteUser/"+id);
  }
  
  updateBooking(newBooking:Booking):Observable<Booking>{
    const correctBooking={
      id:newBooking.id,
      room:newBooking.room,
      user:newBooking.user,
      layout:newBooking.layout,
      title:newBooking.title,
      date:Date.parse(newBooking.date),
      startTime:newBooking.startTime,
      endTime:newBooking.endTime,
      participants:newBooking.participants
    };
    
    
    return this.http.put<Booking>(environment.restUrl+"/api/updateBooking",correctBooking);
  }

  addBooking(newBooking:Booking):Observable<Booking>{
    return of(null);
   }

   cancelBooking(id:number):Observable<any>{
     return this.http.delete(environment.restUrl+"/api/deleteBooking/"+id);
   }

   filterBookingsByDate(selectedDate:string):Observable<Array<Booking>>{
      return this.http.get<Array<Booking>>(environment.restUrl+"/api/booking/"+selectedDate)
        .pipe(
          map( data=>{
            const bookings=new Array<Booking>();
             for(const booking of data){
                 bookings.push(Booking.fromHttp(booking));
             }
             return bookings;
          })
        );
   }

   getUserById(id:number):Observable<User>{
     return this.http.get<User>(environment.restUrl+"/api/user/"+id)
     .pipe(
       map( data=>{
          return User.fromHttp(data);
       })
     );
   }
 
}
