import { Component, OnInit, Input } from '@angular/core';
import { Booking } from 'src/app/models/Booking';
import { ActivatedRoute, Router } from '@angular/router';
import { DataServiceService } from 'src/app/data-service.service';
import { Room, Layout } from 'src/app/models/Room';
import { User } from 'src/app/models/User';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-edit-booking',
  templateUrl: './edit-booking.component.html',
  styleUrls: ['./edit-booking.component.css']
})
export class EditBookingComponent implements OnInit {

  booking:Booking;
  //bookingArray:Array<Booking>;
  rooms:Array<Room>;
  users:Array<User>;
  layouts=Object.keys(Layout);
  layoutEnum=Layout;
  dataLoaded=false;
  message='Please Wait....';
  constructor(private routes:ActivatedRoute,private dataService:DataServiceService,private router:Router) { }

  ngOnInit() {
    /*this.dataService.getBookings().subscribe(
      book=>{this.bookingArray=book}
    );*/

    this.rooms=this.routes.snapshot.data['rooms'];
    this.users=this.routes.snapshot.data['users'];
    const id=this.routes.snapshot.queryParams['id'];
    console.log("edit ID:"+id);

    if(id){
      this.dataLoaded=true;
      this.dataService.getBooking(+id)
        .pipe(
          map(booking=>{
              booking.room=this.rooms.find(room=>booking.room.id===room.id);
              booking.user=this.users.find(user=>booking.user.id===user.id);
              return booking;
          })
        )
        .subscribe(
           next=>{
             this.booking=next;
             this.dataLoaded=true;
             this.message=' ';
           }
        );
    }
    else{
      this.booking=new Booking();
      this.dataLoaded=true;
      this.message=' ';

    }
  }

  onSubmit(){
    if(this.booking.id){
      this.dataService.updateBooking(this.booking).subscribe(
        book=>{this.router.navigate([''])}
      );
    }
    else{
       this.dataService.addBooking(this.booking).subscribe(
          newBook=>{this.router.navigate([''])}
       );
    }
    
  }

}
