export class Room{
    id:number;
    name:string;
    location:string;
    capacities=new Array<LayoutCapacity>();

    static fromHttp(room:Room):Room{
       const newRoom=new Room();
            newRoom.id=room.id;
            newRoom.name=room.name;
            newRoom.location=room.location;
            newRoom.capacities=room.capacities; 
       return newRoom;
    }
}

export class LayoutCapacity{
    layout:Layout;
    capacity:number;
}

export enum Layout{
    THEATER='Theater',
    USHAPE='U-Shape',
    BOARD='Board Meeting'
}