export class User{
    id:number;
    name:string;
    
    getRole():string{    //for checking only 
        return "Managaer";
    }

    static fromHttp(user:User):User{
        const u:User=new User();
        u.id=user.id;
        u.name=user.name;
        return u;
    }

    static arrayFromHttp(user:User[]):User[]{
        const userArray=new Array<User>();
            for(const u of user){
                userArray.push(this.fromHttp(u));
            }
        return userArray;
    }
}