import { Room, Layout } from './Room';
import { User } from './User';
import { pipeBind1 } from '@angular/core';

export class Booking {
    id: number;
    room: Room;
    user: User;
    layout: Layout;
    title: string;
    date: string;
    startTime: string;
    endTime: string;
    participants: number;
    
    getDateAsDate() {
      return new Date(this.date);
    }

    static fromHttp(booking:Booking){
      const b1:Booking=new Booking();
        b1.id=booking.id;
        b1.room=Room.fromHttp(booking.room);
        b1.user=User.fromHttp(booking.user);
        b1.layout=booking.layout;
        b1.title=booking.title;
        b1.date=booking.date;
        b1.startTime=booking.startTime;
        b1.endTime=booking.endTime;
        b1.participants=booking.participants;
      return b1;
    }
  }