import { Component, OnInit } from '@angular/core';
import { formatDate } from '@angular/common';
import { DataServiceService } from '../data-service.service';
import { Booking } from '../models/Booking';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { User } from '../models/User';

@Component({
  selector: 'app-calender',
  templateUrl: './calender.component.html',
  styleUrls: ['./calender.component.css']
})
export class CalenderComponent implements OnInit {

  bookings:Array<Booking>;
  selectedBooking:Booking;
  selectedDate:string;
  dataLoaded=false;
  constructor(private dataService:DataServiceService,
              private router:Router) { 
    
  }
  ngOnInit() {

    this.selectedDate=formatDate(new Date(),'yyyy-MM-dd','en-UK');
     this.setDate();
      /*this.dataService.getBookings().subscribe(
         bookings=>{this.bookings=bookings;}
      );*/
  }

  editBooking(id:number){
      this.router.navigate(['bookings','edit'],{queryParams:{id:id}});
  }

  addBooking(){
    this.router.navigate(['bookings','add']);
  }

  cancelBooking(id:number){
    console.log(id);
    const result=confirm("Are you sure! You want to delete the booking?");

    if(result){
          this.dataService.cancelBooking(id).subscribe(
            (data)=>{
              this.setDate();
            }
          );
    }     
    
  }

  setDate(){
    console.log(this.selectedDate);
    this.dataService.filterBookingsByDate(this.selectedDate).subscribe(
      data=>{
        this.dataLoaded=true,
        this.bookings=data,
              this.router.navigate([''])
      },

    );
  }
}
