import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {Routes, Router, RouterModule} from '@angular/router';
import { AppComponent } from './app.component';
import { MenuComponent } from './menu/menu.component';
import { CalenderComponent } from './calender/calender.component';
import { AdminComponent } from './admin/admin.component';
import { RoomsComponent } from './admin/rooms/rooms.component';
import { UsersComponent } from './admin/users/users.component';
import { RoomDetailComponent } from './admin/rooms/room-detail/room-detail.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { UserDetailComponent } from './admin/users/user-detail/user-detail.component';
import { EditUserComponent } from './admin/users/edit-user/edit-user.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { RoomEditComponent } from './admin/rooms/room-edit/room-edit.component';
import { EditBookingComponent } from './calender/edit-booking/edit-booking.component' 
import { BookingsComponent } from './bookings/bookings.component';
import {HttpClientModule} from '@angular/common/http';

import { PreFetchRoomsService } from './pre-fetch-rooms.service';
import { PreFetchUsersService } from './pre-fetch-users.service';
import { LoginComponent } from './login/login.component';
import { AuthRouterGuardService } from './auth-router-guard.service';
//import {HttpClientModule} from '@angular/common';

const routes:Routes=[
  {path:"admin/users",component:UsersComponent,canActivate:[AuthRouterGuardService]},
  {path:"admin/rooms",component:RoomsComponent,canActivate:[AuthRouterGuardService]},
  {path:"",component:CalenderComponent},
  {path:"login",component:LoginComponent},
  {path:"bookings/edit",component:EditBookingComponent, resolve:{rooms:PreFetchRoomsService,users:PreFetchUsersService},canActivate:[AuthRouterGuardService]},
  {path:"bookings/add",component:EditBookingComponent, resolve:{rooms:PreFetchRoomsService,users:PreFetchUsersService},canActivate:[AuthRouterGuardService]},
  {path:"bookings",component:BookingsComponent},
  {path:"404",component:PageNotFoundComponent},
  {path:"**",redirectTo:'/404'}
];

@NgModule({
  declarations: [
    AppComponent,
    MenuComponent,
    CalenderComponent,
    AdminComponent,
    RoomsComponent,
    UsersComponent,
    RoomDetailComponent,
    PageNotFoundComponent,
    UserDetailComponent,
    EditUserComponent,
    RoomEditComponent,
    EditBookingComponent,
    BookingsComponent,
    LoginComponent

  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot(routes),
    FormsModule,
    ReactiveFormsModule
    
  ],
  providers: [PreFetchRoomsService,PreFetchUsersService],
  bootstrap: [AppComponent]
})
export class AppModule { }
