import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { User } from 'src/app/models/User';
import { Router, Data } from '@angular/router';
import { DataServiceService } from 'src/app/data-service.service';

@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.css']
})
export class UserDetailComponent implements OnInit {

  @Input()
  userToBeShown:User;
  message:string='';

  @Output()
  deleteUserEvent=new EventEmitter();
  constructor(private router:Router,private dataService:DataServiceService) { }

  ngOnInit() {
  }

  editUser(id:number){
      this.router.navigate(['admin','users'],{queryParams:{id:id,action:'edit'}});
  }

  deleteUser(){
    this.dataService.deleteUser(this.userToBeShown.id).subscribe(
      user=>{
        this.deleteUserEvent.emit();
        this.router.navigate(['admin','users']);
      },
      error=>this.message='Something went wrong!!!'
    );
  }

  pageNotFound(){
    this.router.navigate(['404']);
  }
}
