import { Component, OnInit } from '@angular/core';
import { DataServiceService } from 'src/app/data-service.service';
import { Room } from 'src/app/models/Room';
import { Router, ActivatedRoute } from '@angular/router';
import { ResetFormService } from 'src/app/reset-form.service';

@Component({
  selector: 'app-rooms',
  templateUrl: './rooms.component.html',
  styleUrls: ['./rooms.component.css']
})
export class RoomsComponent implements OnInit {

  room:Room[];
  selectedRoom:Room;
  actionInRoomComponent:string;
  constructor(private dataService:DataServiceService,
              private router:Router,  
              private route:ActivatedRoute,
              private formResetService:ResetFormService) { }

  loadingData=true;
  message:string='Please wait . . .';
  reloadTimes=0;
  ngOnInit() {
    this.loadData();
  }

  processAfterLoading(){
    this.route.queryParams.subscribe(
      (params)=>{
        const id=params['id'];
        this.actionInRoomComponent=params['action'];
        if(id){
          // -----------------
          this.selectedRoom=this.room.find(
            (room)=>{
              return room.id===+id;
            }
          );
          }
          //---------------
          if(params['action']==='add'){
            this.selectedRoom=new Room();
            this.formResetService.resetRoomEvent.emit(this.selectedRoom);
          }
        
      
      }
    );
  }

  loadData(){
    this.dataService.getRooms().subscribe(
      (rooms)=>{
        this.room=rooms;
        this.processAfterLoading();
         this.loadingData=false;
         console.log(rooms);
      },
      (error)=>{
         if(error.status===402){
          this.message='Sorry! You need to pay for accessing !';
         }
         else{
          this.reloadTimes++;
              if(this.reloadTimes<10){
                this.message='Something went wrong!  Redirecting . . . '+this.reloadTimes;
                this.loadData();
              }
              else{
                this.message='Sorry! Cant connect to server . Please contact support!'
              }
         }
      }

    );
  }


  selectedRoomFunction(id:number){
     this.router.navigate(['admin','rooms'],{queryParams:{id:id,action:'view'}});
      
  }

  navigateAddRoom(){
    this.router.navigate(['admin','rooms'],{queryParams:{action:'add'}})
  }

}
